@import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .brdr {
    @apply border-[#1E2D3D];
  }

  .bg {
    @apply bg-[#011627];
  }

  .bg-3 {
    @apply bg-[#1E2D3D];
  }

  .t-a {
    @apply transition-all duration-200;
  }

  .tc {
    @apply text-[#607B96];
  }

  .tc-g {
    @apply text-[#43D9AD];
  }
  .tc-gd {
    @apply text-[#3C9D93];
  }

  .tc-l {
    @apply text-[#1E2D3D];
  }

  .tc-b {
    @apply text-[#4D5BCE];
  }

  .tc-r {
    @apply text-[#E99287];
  }

  .tc-o {
    @apply text-[#FEA55F];
  }
  .tc-p {
    @apply text-[#C98BDF];
  }

  .fc {
    @apply fill-[#607B96];
  }

  .btn {
    @apply border-none outline-none ;
  }
  .btn-b {
    @apply outline-none ;
  }

  .btn-def {
    @apply w-max bg-[#1C2B3A] px-[14px] py-[10px] rounded-lg ;
  }
}


@layer utilities {
  /* Chrome, Safari and Opera */
  .no-scrollbar::-webkit-scrollbar {
    display: none;
  }

  .no-scrollbar {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
  }
}

* {
  font-family: 'Fira Code', monospace;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}